@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #0e1936;
  width: 100vw;
  height: 100vh;
  background-size: auto 100vh;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center bottom;
  font-family: 'Montserrat', sans-serif;
  min-width: 1130px;
}

.App-background-text {
  background-image: url(/static/media/background-text.e1ee0b4a.png);
}

.App-background-notext {
  background-image: url(/static/media/background.b3fee62b.png);
}

#Core-Text {
  font-size: 10vh;
  text-align: left;
  color: #FCE918;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

#About {
  color: #FCE918;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  max-width: 50vw;
  font-size: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: left;
  /* text-justify: inter-word; */
}

.App-header {
  /* background-color: #282c34; */
  /* min-height: 100vh; */
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: column; */
  /* align-items: center; */
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 30px 20px 0px 20px;
  font-size: 48px;
}

#details {
  font-size: 24px;
  font-weight: 500;
}

.number {
  font-weight: normal;
  font-size: 21px;
}

.Header-item {
  /* margin: 20px; */
  text-align: left;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
  line-height: 50px;
  margin-left: 30px;
  vertical-align: text-bottom;
}

.Header-item:hover {
  color: #f6df00;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: white;
}

.selected {
  font-size: 40px;
  color: #f6df00;
}

#Desktop-header-left {
  align-items: left;
  justify-self: flex-start;
  text-align: left;
  font-weight: 600;
  line-height: 30px;
  font-size: 48px;
}

#Desktop-header-left:hover {
  cursor: pointer;
}

/* #Desktop-header-space {
  flex-grow: 3;
} */

#Desktop-header-right {
  /* align-items: right; */
  /* justify-self: flex-end; */
  display: flex;
  margin-left: auto;
  flex-direction: row;
  margin-top: 10px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

